import Vue from 'vue'
import VueRouter from 'vue-router'
import Layout from '@/layout'

const Dashboard = () => import('@/views/dashboard')
const Categories = () => import('@/views/categories')
const Rank = () => import('@/views/rank')
const Booklists = () => import('@/views/booklists')
const CategoryInfo = () => import('@/views/categoryInfo')
const Searching = () => import('@/views/searching')
const SearchResult = () => import('@/views/searchResult')
const BookInfo = () => import('@/views/bookInfo')
const BooklistsDetail = () => import('@/views/booklistsDetail')

Vue.use(VueRouter)

export const routes = [
  {
    path: '/',
    component: Layout,
    redirect: '/dashboard',
    children: [
      {
        path: '/dashboard',
        component: Dashboard,
        name: 'Dashboard',
        meta: { title: '书架', icon: 'column' }
      },
      {
        path: '/categories',
        component: Categories,
        name: 'Categories',
        meta: { title: '分类', icon: 'cluster' }
      },
      {
        path: '/rank',
        component: Rank,
        name: 'Rank',
        meta: { title: '排行', icon: 'hot' }
      },
      {
        path: '/booklists',
        component: Booklists,
        name: 'Booklists',
        meta: { title: '书单', icon: 'goods-collect' }
      }
    ]
  },
  {
    path: '/categoryInfo',
    component: CategoryInfo,
    name: 'CategoryInfo',
  },
  {
    path: '/searching',
    component: Searching,
    name: 'Searching'
  },
  {
    path: '/searchResult',
    component: SearchResult,
    name: 'SearchResult'
  },
  {
    path: '/bookInfo/:id',
    component: BookInfo,
    name: 'BookInfo'
  },
  {
    path: '/booklists/:id',
    component: BooklistsDetail,
    name: 'BooklistsDetail'
  }
]

const router = new VueRouter({
  routes
})

export default router
