<template>
    <div class="rank-list">
        <van-divider>{{title}}</van-divider>
        <ul class="content">
            <li v-for="item in list" :key="item.bookCount" @click="toCategoryInfo(item.name)">
                <div class="left">
                    <p class="left-title">{{item.name}}</p>
                    <p class="left-count">{{item.monthlyCount}} 本</p>
                </div>
                <div class="right">
                    <img class="pic1" :src="item.bookCover[0] | fixImg" />
                    <img class="pic2" :src="item.bookCover[1] | fixImg" />
                    <img class="pic3" :src="item.bookCover[2] | fixImg" />
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    name: 'RankList',
    props: {
        title: {
            required: true,
            type: String
        },
        list: {
            required: true,
            type: Array
        },
        gender: {
            required: true,
            type: String
        }
    },
    methods: {
        toCategoryInfo(major) {
            this.$router.push({ path: '/categoryInfo', query: { gender: this.gender, major: major }})
        }
    }
}
</script>

<style lang="scss" scoped>
.rank-list {
    margin-bottom: 50px;
    overflow: auto;
    .content {
        li {
            width: 110px;
            float: left;
            background-color: #f0f0f0;
            padding: 10px 10px 0 10px;
            margin: 0 0 5px 10px;
            .left {
                float: left;
                .left-title {
                    font-size: 12px;
                    padding-top: 5px;
                    color: #333;
                }
                .left-count {
                    font-size: 10px;
                    padding-top: 5px;
                    color: #BBBBBB;
                }
            }
            .right {
                float: right;
                position: relative;
                width: 45px;
                height: 40px;
                img {
                    position: absolute;
                }
                .pic1 {
                    width: 27px;
                    height: 36px;
                    left: 0;
                    top: 4px;
                }
                .pic2 {
                    width: 30px;
                    height: 40px;
                    left: 10px;
                    z-index: 2;
                }
                .pic3 {
                    width: 24px;
                    height: 32px;
                    right: 0;
                    top: 8px;
                }
            }
        }
    }
}
</style>