<template>
    <van-tabbar 
    v-model="active" 
    :z-index=3 
    active-color="#ee0a24"
    route>
        <van-tabbar-item 
        v-for="item in tabList" :key="item.path" 
        :icon="item.meta.icon"
        :to="item.path"
        :name="item.meta.title">
            {{item.meta.title}}
        </van-tabbar-item>
    </van-tabbar>
</template>

<script>
export default {
    name: 'Tabbar',
    data() {
        return {
            active: "书架"
        }
    },
    props: {
        tabList: {
            required: true,
            type: Array
        }
    }
}
</script>