<template>
  <div class="layout">
    <router-view class="content" :key="$route.fullPath"/>
    <tabbar class="tabbar" :tabList="tabList" />
  </div>
</template>

<script>
import { Tabbar } from '@/components'
import { routes } from '@/router'
export default {
  name: 'Layout',
  components: {
    Tabbar
  },
  data() {
      return {
          // tabList: routes[0].children
      }
  },
  computed: {
    tabList() {
      return routes[0].children.filter(e => e.meta)
    }
  }
}
</script>

<style lang="scss" scoped>
.layout {
  position: relative;
  .tabbar {
    position: fixed;
    bottom: 0;
  }
}
</style>