<template>
    <van-tabs v-model="active" sticky>
        <van-tab title="我的书架" class="my-bookshelf">
            <div class="my-bookshelf-title" v-if="this.bookshelf.length">
                <div>
                    <div class="length">共{{this.bookshelf.length}}本</div>
                    <div class="edit" @click="edit()" v-if="!isEdit">编辑</div>
                    <div class="edit" @click="checkAll" v-else>全选</div>
                </div>
            </div>
            <van-checkbox-group v-model="result" ref="checkboxGroup">
                <van-cell-group>
                    <van-cell
                    v-for="(item, index) in bookshelf"
                    clickable
                    :key="index"
                    @click="toggle(index, item.id)"
                    >
                        <template slot="icon">
                            <img :src="item.cover | fixImg" />
                        </template>
                        <template slot="title">
                            <h2>{{item.title}}</h2>
                        </template>
                        <template slot="label">
                            <div class="textOverflow1">
                                <span>{{item.updated | formatDate}}：</span>
                                <span>{{item.lastChapter}}</span>
                            </div>
                        </template>
                    <van-checkbox
                        :name="item.id"
                        ref="checkboxes"
                        slot="right-icon"
                        checked-color="#ee0a24"
                        v-if="isEdit"
                    />
                    </van-cell>
                </van-cell-group>
            </van-checkbox-group>
            <div class="bottom-bar" v-if="isEdit">
                <div class="add" @click="cancel()">取消</div>
                <div class="begin-read" @click="del()">删除</div>
            </div>
        </van-tab>
        <van-tab title="最近读的" class="recent">
            <div @click="toCategory">
                去书城逛逛
            </div>
        </van-tab>
    </van-tabs>
</template>

<script>
import { mapState, mapMutations } from 'vuex'

export default {
    name: 'Tab',
    data() {
        return {
            active: 0,
            isEdit: false,
            result: []
        };
    },
    computed: {
        ...mapState([
            'bookshelf'
        ])
    },
    methods: {
        ...mapMutations([
            'Del_BOOKSHELF',
            'NEW_BOOKSHELF'
        ]),
        toggle(index, id) {
            if(!this.isEdit) {
                this.$router.push({name: 'BookInfo', params: {id: id}})
            } else {
                this.$refs.checkboxes[index].toggle();
            }
            
        },
        toCategory() {
            this.$router.push({path: '/categories'})
        },
        edit() {
            this.$refs.checkboxGroup.toggleAll(false);
            this.isEdit = true
        },
        cancel() {
            this.$refs.checkboxGroup.toggleAll(false);
            this.result = []
            this.isEdit = false
        },
        del() {
            if(!this.result.length) {
                this.$toast('请选择');
            } else {
                this.$dialog.confirm({
                    message: '确认删除吗？',
                    confirmButtonColor: '#ee0a24'
                }).then(() => {
                    // 遍历数组，找出未被选中的项，组成新的数组
                    for(let i =0; i<this.result.length; i++) {
                        let arr = []
                        this.bookshelf.forEach(e => {
                            if(e.id !== this.result[i]) {
                                arr.push(e)
                            }
                        })
                        this.NEW_BOOKSHELF(arr)
                    }
                    
                    this.isEdit = false
                }).catch(() => {
                    // on cancel
                });
            }
            
        },
        checkAll() {
            this.$refs.checkboxGroup.toggleAll(true);
        },
    }
}
</script>

<style lang="scss" scoped>
.my-bookshelf {
    padding-bottom: 2.6666rem;
    .my-bookshelf-title {
        div {
            display: flex;
            justify-content: space-between;
            font-size: 16px;
            font-weight: 700;
            padding: 10px 0;
            .length {
                color: #999;
            }
            .edit {
                color: #ee0a24;
            }
        }
    }
    img {
        width: 36px;
        height: 48px;
        border-radius: 2px;
        box-shadow: -2px 2px 4px -2px #ccc,2px 2px 4px -2px #ccc;
        margin-right: 5px;
    }
    h2 {
        font-size: 14px;
        color: #333;
        font-weight: 700;
    }
    .textOverflow1 {
        width: 240px;
        font-size: 10px;
        font-weight: 700;
    }
    .bottom-bar {
        border-top: 1px solid #ddd;
        height: 50px;
        position: fixed;
        bottom: 1.3333rem;
        z-index: 100;
        width: calc(100% - 20px);
        background-color: #fff;
        display: flex;
        justify-content: space-around;
        align-items: center;
        div {
            width: 150px;
            height: 35px;
            text-align: center;
            line-height: 35px;
            border-radius: 20px;
            font-size: 14px;
        }
        .add {
            background-color: #FFCCCC;
            color: #FF6600;
        }
        .begin-read {
            background-color: #FF0033;
            color: #fff;
        }
    }
}
.recent {
    div {
        width: 100px;
        height: 40px;
        border-radius: 15px;
        background-color: #ee0a24;
        text-align: center;
        line-height: 40px;
        color: #fff;
        font-size: 16px;
        margin-top: -20px;
        margin-left: -50px;
        position: relative;
        left: 50%;
        top: 50vh;
    }
}
</style>