import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    bookshelf: [],
    searchHistory: []
  },
  getters: {

  },
  mutations: {
    ADD_BOOKSHELF: (state, bookmsg) => {
      state.bookshelf.push(bookmsg)
    },
    Del_BOOKSHELF: (state, bookmsg) => {
      state.bookshelf.splice(state.bookshelf.findIndex(item => item.id === bookmsg.id), 1)
    },
    ADD_SEARCHHISTORY: (state, title) => {
      if(state.searchHistory.some(item => item === title)) return
      state.searchHistory.push(title)
    },
    DEL_ALLSEARCHHISTORY: (state) => {
      state.searchHistory = []
    },
    NEW_BOOKSHELF: (state, _bookshelf) => {
      state.bookshelf = _bookshelf
    }
  },
  actions: {
  },
  modules: {
  }
})
